<template>
  <div class="home">
    
    <!-- <Item msg="df"></Item> -->

    <mt-search v-model="value"
      cancel-text="取消"
      placeholder="搜索"
      :show='true'>
      <mt-cell
        v-for="(item,index) in result"
        :key = index
        :title="item.title"
        :value="item.value"
        :to="`/detail?key=${item.title}`">
      </mt-cell>
    </mt-search>
    
  </div>
</template>

<script>
// @ is an alias to /src
import Item from "@/components/Item.vue";
import  axios from "axios";

export default {
  name: "home",
  components: {
    Item
  },
  data() {
    return {
      value: "",
      result: [
        {
          title: "出国申请",
          value: "待审批"
        },
        {
          title: "打印机申请",
          value: "待审批"
        }, {
          title: "电脑申请",
          value: "待审批"
        }, {
          title: "显示器申请",
          value: "已审批"
        }, {
          title: "座位申请",
          value: "已审批"
        }
      ]
    };
  },
  methods: {},
  mounted() {
    console.log(this.$route.query)
     axios.get('http://192.168.43.125:3000/posts').then(res=>{
        this.result = res.data[0].res
      })
  }
};
</script>
<style>
.mint-search-list{
  padding-top: 92px;
}
</style>



