<template>
  <div class="details">
    <!-- <mt-popup
      v-model="popupVisible"
      position="top"
      modal=false>
      <div>sdfsf</div>
    </mt-popup> -->

    <mt-field label="用户名" placeholder="请输入用户名" v-model="username"></mt-field>
    <mt-field label="生日" placeholder="请输入生日" type="date" v-model="birthday"></mt-field>
    <mt-field label="自我介绍" placeholder="自我介绍" type="textarea" rows="4" v-model="introduction"></mt-field>
    <div style="width: 80%;margin: auto;margin-top:50px">

      <mt-button style="width: 100%;" type="primary" plain size="small" @click="getDatabyc">通过</mt-button>
    </div>
    
  </div>
</template>

<script>
import { MessageBox } from "mint-ui";
import  axios from "axios";

export default {
  name: "Details",
  props: {
    msg: String
  },
  data() {
    return {
      username: "",
      birthday: "2018-01-16",
      introduction:
        "需要出国深造,需要出国深造,需要出国深造,需要出国深造,需要出国深造,需要出国深造"
      // popupVisible:true
    };
  },
  methods: {
    getDatabyc() {
      //  this.popupVisible = true
      //  console.log(this.introduction)
      MessageBox("提示", "操作成功");
      this.$router.go(-1);

      // MessageBox({
      //   title: "提示",
      //   message: "确定执行此操作?",
      //   showCancelButton: true
      // }).then(action => {
      //   console.log("sdfsdf");
      //   this.$router.go(-1);
      // });
    },
    
    getDatabyApi() {
      axios.get('http://192.168.43.125:3000/comments').then(res=>{
        let data = res.data[0].res
        this.username = data.username
        this.birthday = data.birthday
        this.introduction = data.introduction

      })
    }
  },
  mounted() {
    // console.log(this.$router);
    this.getDatabyApi()
  }
};
</script>

