<template>
  <div id="app">
    <!-- <div id="nav">
      <router-link to="/">Home</router-link> |
      <router-link to="/about">About</router-link>
    </div> -->

    <router-view class="content"/>
    
    <mt-header fixed :title="title">
      <router-link v-if="title=='审批详情'" to="/" slot="left">
        <mt-button icon="back">返回</mt-button>
      </router-link>
    </mt-header>

  </div>
</template>
<script>
// @ is an alias to /src
import Item from "@/components/Item.vue";

export default {
  name: "app",
  components: {
    Item
  },
  watch: {
    "$route.name"(n, o) {
      this.cgtl(n);
    }
  },
  data() {
    return {
      title: ""
    };
  },
  methods: {
    cgtl(n) {
      if (n == "home") this.title = "审批列表";
      else this.title = "审批详情";
    }
  },
  mounted() {
    let n = this.$route.name;
    this.cgtl(n);
  }
};
</script>

<style lang="scss">
body,
html {
  margin: 0px;
  padding: 0px;
}
#app {
  //   font-family: 'Avenir', Helvetica, Arial, sans-serif;
  //   -webkit-font-smoothing: antialiased;
  //   -moz-osx-font-smoothing: grayscale;
  //   text-align: center;
  color: #2c3e50;
}

.content {
  margin-top: 40px;
}
</style>



